body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.phantom thead, .phantom tbody {
  display: none;
}

.App {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App .container {
  margin: 48px;
  margin-bottom: 96px;
}

.App .btn.btn-default {
  visibility: hidden;
  position: relative;
  width: 200px;
}

.App .btn.btn-default:after {
  width: 200px;
  display: inline-block;
  padding: 0.5em 1.2em;
  margin: 0 0.3em 0.3em 0;
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #2196f3;
  text-align: center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
  visibility: visible;
  position: absolute;
  top: 0;
  left: 0;
  content: "Download Excel file";
}

.App .btn.btn-default:hover:after {
  background-color: #4095c6;
}

.pdf-file-picker {
  height: 100%;
  width: 100%;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
}

.pdf-file-picker .drop-zone {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.pdf-file-picker .loading-container {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

